import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import pageNoteFound from '../views/notFound.vue'
import login from '../views/auth/login.vue'
Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home
  }, 
  {
    path: "/degree",
    name: "Degree",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/Degree.vue")
  },
  {
    path: "/programs",
    name: "Programs",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/Programs.vue")
  },
  {
    path: "/events",
    name: "Events",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/Events.vue")
  },
 
  
  {
    path: '/login',
    component: login,
    name: 'login',
    beforeEnter: (to, from, next) => {
      if(localStorage.getItem('admin')){
        window.location.href = '/dashboard'
      }else{
        next()
      }
    }
  },
  {
    path: '/dashboard',
    name: 'dashboard',
    component: ()=> import('@/views/auth/dashboard.vue'),
    beforeEnter: (to, from, next) => {
      if(!localStorage.getItem('admin')){
        window.location.href = '/login'
      }else{
        next()
      }
    }
  },
  {
    path: '/:pathMatch(.*)*', 
    component: pageNoteFound
  },
  
];


const router = new VueRouter({
  mode: "history",
  // base: process.env.BASE_URL,
  routes
  // scrollBehavior: to => {
  //   if (to.hash) {
  //     const toHash = document.querySelector(to.hash);
  //     return window.scrollTo({
  //       top: toHash.offsetTop,
  //       behavior: "smooth"
  //     });
  //   }
  //   return window.scrollTo({ top: 0, behavior: "smooth" });
  // }
});


export default router;
