<template>
  <div class="container new">
    <div style="width: 400px;">
      <form @submit.prevent>
        <v-text-field v-model="auth.username" :counter="10" label="Username" required></v-text-field>

        <v-text-field v-model="auth.password" :counter="8" type="password" label="Password" required></v-text-field>
        <v-btn @click="login" class="me-4 primary">
          submit
        </v-btn>
        <v-btn @click="reset" class="red">
          clear
        </v-btn>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      auth: {
        username: '',
        password: ''
      }
    }
  },
  methods: {
    login() {
      if(this.auth.username === 'sherzod' && this.auth.password === 'admin'){
        this.$router.push({name: 'dashboard'})
        window.localStorage.setItem('admin',JSON.stringify(this.auth))
      }else{
        alert('Siz admin emassiz!')
        this.auth = {
          username: '',
          password: ''
        }
      }
    },
    reset() {
      this.username = ''
      this.password = ''
    }
  }
};
</script>

<style scoped>
.new {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 80vh;
}
</style>
